import { Component, OnInit } from '@angular/core';
import { ProductDirectoryBean } from './bean/product-directory.bean';
import { SelectItem } from 'primeng/primeng';
import { ProductManageHttpService } from "./../../http/product-manage.http.service";
import { ConfirmationService } from 'primeng/primeng';//
import { Message } from 'primeng/primeng';
import { tableMessage } from 'app/@uisftech/common/constant/config';
import { Commfunc } from "app/@uisftech/common/service/commonFun";
import { Router } from '@angular/router';

@Component({
  selector: 'app-product-directory',
  templateUrl: './product-directory.component.html',
  styleUrls: ['./product-directory.component.scss'],
  providers: [ProductManageHttpService, ConfirmationService, Commfunc]
})
/**
 * //-
 */
export class ProductDirectoryComponent implements OnInit {
  orgTreeDisplayG: boolean = false;
  first: number = 0;// 
  tabMesg: any = tableMessage;//
  CatalogType: any = [];
  code: any;
  // bean
  ProductDirectoryBean: ProductDirectoryBean = new ProductDirectoryBean();
  //
  all_title: string = '';
  //
  updateValue: any = [];
  //
  cars_2: any;
  arr_2: any;
  //
  msgs: Message[] = [];
  //
  total: string;
  //
  pageSize: string = '10';
  //
  showModel: string;

  display: boolean = false;
  //
  headerTitle: string;
  //
  belongDepart: string;
  ownerOrgId: string;
  departOrStaffPost: string;
  constructor(
    public commfunc: Commfunc,
    public router: Router,
    public productManageHttpService: ProductManageHttpService,
    public confirmationService: ConfirmationService) {
    this.codeValues() //
    this.CatalogType = this.code['CatalogType'];
  }

  ngOnInit() {
    this.first = 0;
    this.queryClick();
  }
  //
  codeValues() {
    this.commfunc.codeValue();
    let ret = this.commfunc.codeDatas;
    return this.code = ret //code
  }

  // 
  resetClick() {
    this.ProductDirectoryBean = new ProductDirectoryBean();
    this.departOrStaffPost = '';
  }
  //
  querySecle() {
    this.ProductDirectoryBean.pageSize = 10;
    this.ProductDirectoryBean.pageNum = 1;
    this.first = 0;
    this.queryClick();
  }
  //  
  queryClick() {
    this.productManageHttpService.catalogManageQueryList(this.ProductDirectoryBean).subscribe(data => {
      if (data.returnCode.code == "success") {
        this.cars_2 = data.catalogManageInfoList;
        this.total = data.total;
      } else {
        this.msgs = [];
        this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
      }
    }, error => {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: '' });
    })
  }
  //
  deleteClick(item) {
    this.confirmationService.confirm({
      message: '?',
      accept: () => {
        //
        let param = { ctlgId: item.prodCatalogId };
        this.productManageHttpService.catalogDelete(param).subscribe(data => {
          if (data.returnCode.code == "success") {
            this.msgs = [];
            this.msgs.push({ severity: 'success', summary: '', detail: data.returnCode.message });
            this.first = 0;
            this.queryClick();
          } else {
            this.msgs = [];
            this.msgs.push({ severity: 'error', summary: '', detail: data.errorMessage });
          }
        }, error => {
          this.msgs = [];
          this.msgs.push({ severity: 'error', summary: '', detail: "" });
        })
      }
    });
  }
  detailsClick(item) {
    let data;
    data = JSON.stringify(item);
    this.router.navigate(['/pages/tzb/product-manage/product-summary/business-directory-details', { data: data }]);
  }
  modifyClick(item) {
    let data;
    data = JSON.stringify(item);
    this.router.navigate(['/pages/tzb/product-manage/product-summary/business-directory-modify', { data: data }]);
  }
  // 
  sheMain() {
    this.orgTreeDisplayG = !this.orgTreeDisplayG;
  }
  // 
  treeCallG(param) {
    this.ownerOrgId = '';
    this.belongDepart = '';
    this.orgTreeDisplayG = false;
    if (param.length > 1) {
      this.msgs = [];
      this.msgs.push({ severity: 'error', summary: '', detail: "" });
    } else {
      param.forEach(items => {
        this.belongDepart = items.orgName;
        this.ownerOrgId = items.orgId;
      });
    }
    this.ProductDirectoryBean.ownerOrgId = this.ownerOrgId;
    this.departOrStaffPost = this.belongDepart;
  }
  //
  paginate(event) {
    //
    this.ProductDirectoryBean.pageSize = event.rows;
    //
    let currentPage = event.page + 1;
    this.ProductDirectoryBean.pageNum = currentPage;
    this.first = (currentPage - 1) * this.ProductDirectoryBean.pageSize;
    //
    this.queryClick();
    this.ProductDirectoryBean.pageNum = 1;
  }
  //
  permissionCheck(id) {
    return this.commfunc.permissionCheck(id)
  }

}
